body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Board{
	width: 500px;
  height: 500px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}
.row{
	display:block;
	height: 50px;
	width: 500px;
}

.unmarkedField{
	display: inline;
	width: 50px;
	height: 50px;
	font-size: 30px;
	background: #fff;
  border: 1px solid #999;
  float: left;
  font-weight: bold;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  border-radius: 25%;
}

.markedField{
	display: inline;
	width: 50px;
	height: 50px;
	font-size: 30px;
	background: #ff0;
  border: 1px solid #999;
  float: left;
  font-weight: bold;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  border-radius: 25%;
}

.overtop {
	display: -webkit-flex;
	display: flex;
	text-align: center;
	width: 500px;
	height: 50px;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

.top {
	display: -webkit-flex;
	display: flex;
	text-align: center;
	width: 500px;
	height: 100px;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	border: 1px solid blue;
	margin-bottom: 10px;
}

#banner {
	display: -webkit-flex;
	display: flex;
	text-align: center;
	width: 300px;
	height: 100px;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

#time {
	display: -webkit-flex;
	display: flex;
	text-align: center;
	width: 200px;
	height: 100px;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

.timer {
	display: -webkit-inline-flex;
	display: inline-flex;
	text-align: center;
	width: 150px;
	height: 75px;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	font-size: 50px;
	background-color: #000;
	color: #0f0;
}

.refresh {
	height: 40px;
	border: 1px solid #f00;
	background: #fff;
	font-size: 25px;
	font-weight: bold;
	text-align: center;
	padding: 5px 15px 5px 15px;
	box-sizing: border-box;
	margin-left: 20px;
}

